import React, {Fragment} from 'react';
import ResponsiveContainer from 'recharts/lib/component/ResponsiveContainer';
import LineChart from 'recharts/lib/chart/LineChart';
import Line from 'recharts/lib/cartesian/Line';
import XAxis from 'recharts/lib/cartesian/XAxis';
import YAxis from 'recharts/lib/cartesian/YAxis';
import CartesianGrid from 'recharts/lib/cartesian/CartesianGrid';
import Tooltip from 'recharts/lib/component/Tooltip';
import Legend from 'recharts/lib/component/Legend';
import Typography from '@material-ui/core/Typography';

class MyLineChart2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data:[]
        };
    }

    convert(data) {
        var mydata = [];
        for (var i in data.data) {
            var line = {};
            for (var j in data.label) {
                if (data.label[j] === "year") {
                    line[data.label[j][0]] = data.data[i][j]
                }
                else {
                    line[data.label[j][0]] = parseFloat(data.data[i][j])
                }
            }
            mydata.push(line);
        }
        return mydata
    }

    componentWillMount() {
        this.setState(state => {
            state.data = this.convert(this.props.data)
            return state
        })
    }

    render() {
        const {data} = this.state;
        //console.log(data)
        return (
            <Fragment>
                <Typography variant="h5" gutterBottom>
                กราฟงบประมาณแต่ละหน่วยงานย่อยภายใต้กระทรวงทรัพยากรธรรมชาติและสิ่งแวดล้อม
                แต่ละปีตั้งแต่ พ.ศ. 2552-2562
                </Typography>
                <ResponsiveContainer width="99%" height={320}>
                    <LineChart data={data}>
                        <XAxis dataKey="year" />
                        <YAxis />
                        <CartesianGrid vertical={false} strokeDasharray="3 3" />
                        <Tooltip />
                        <Legend />
                        { this.props.data.label.map((line, i) => (
                            line[0] !== "year" ? <Line type="monotone" key={i} dataKey={line[0]} stroke={line[1]} activeDot={{ r: 8 }}/>:null
                        ))}
                    </LineChart>
                </ResponsiveContainer>
            </Fragment>
        );
    }
}

export default MyLineChart2;